$small: 767px;

@media only screen and (max-width: $small) {
  .popup__tag,
  .title--hide,
  .component__signup {
    display: none; }

  .component--hide {
    display: flex; }

  .popup {
    position: relative;
    background-color: $white;
    border-radius: 0;
    margin-top: 0;
    max-width: 500px; }

  .popup__content {
    margin: 0; }

  .popup__header {
    justify-content: center;
    margin-bottom: 15px; }

  .popup__title {
    font-size: 18px;
    margin: auto;
    margin: 0; } // IE fix to center properly

  .popup__dismiss {
    position: absolute;
    width: 14px;
    height: 14px;
    right: 0;
    margin-right: 20px;
    //overflow: hidden
    cursor: pointer;
    &:hover {
      &::before, &::after {
        background: $border; } }

    &::before, &::after {
      content: '';
      position: absolute;
      height: 2px;
      width: 100%;
      left: 0;
      top: 50%;

      margin-top: -1px;
      background: $text; }
    &::before {
      transform: rotate(45deg); }
    &::after {
      transform: rotate(-45deg); } }


  .component {
    flex-grow: 0; // No shorthand property for IE
    flex-shrink: 1;
    flex-basis: 100%;
    max-width: 100%;
    justify-content: center;
    margin-left: 15px;
    margin-right: 15px; } }
